         grid-gap: 15px;\r\n                padding: 15px;\r\n                align-items: start;\r\n                @media screen and (max-width: 700px) {\r\n                    grid-template-columns: 1fr;\r\n                }\r\n            }\r\n\r\n            .actions {\r\n                display: flex;\r\n                justify-content: flex-end;\r\n                flex-wrap: wrap;\r\n                .button {\r\n                    margin: 5px;\r\n                }\r\n            }\r\n\r\n            .styleWrapper {\r\n                position: relative;\r\n                width: 100%;\r\n                display: flex;\r\n                align-self: stretch;\r\n                justify-content: center;\r\n                align-items: center;\r\n\r\n                img {\r\n                    min-width: 290px;\r\n                    width: 100%;\r\n                    object-fit: fill;\r\n                }\r\n                label {\r\n                    transition: all 0.1s ease-in-out;\r\n                    margin-top: 5px;\r\n                }\r\n                .active {\r\n                    outline: 5px solid #d54675;\r\n                }\r\n                input {\r\n                    display: none;\r\n                }\r\n                .imgWrapper {\r\n                    position: relative;\r\n                    .pro_feature_promo {\r\n                        position: absolute;\r\n                        top: 0;\r\n                        left: 0;\r\n                        width: 0;\r\n                        height: 0;\r\n                        z-index: 20;\r\n                        background: #0000007d;\r\n                        overflow: hidden;\r\n                        transition: all 0.1s ease-in-out;\r\n                        a {\r\n                            margin: 0;\r\n                            position: absolute;\r\n                            top: 65%;\r\n                            left: 50%;\r\n                            transform: translateX(-50%);\r\n                            .svg_icons {\r\n                                all: unset;\r\n                            }\r\n                        }\r\n                        .promo_close_btn {\r\n                            display: none;\r\n                        }\r\n                        strong {\r\n                            position: absolute;\r\n                            top: 45%;\r\n                            left: 50%;\r\n                            transform: translate(-50%, -50%);\r\n                            white-space: nowrap;\r\n                            color: white;\r\n                            font-size: 15px;\r\n                        }\r\n                        &.active {\r\n                            width: 100%;\r\n                            height: 100%;\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n    .tableStyleModal.active {\r\n        pointer-events: all;\r\n        background: rgba(0, 0, 0, 0.52);\r\n    }\r\n\r\n    .promo_large {\r\n        display: flex;\r\n        justify-content: center;\r\n        align-items: center;\r\n        width: 99%;\r\n        height: 100%;\r\n        top: 0;\r\n        left: 0;\r\n        overflow: hidden;\r\n        pointer-events: none;\r\n        z-index: 99;\r\n        background: #00000085;\r\n        position: absolute;\r\n        transition: opacity 300ms ease-in-out;\r\n\r\n        & > div {\r\n            justify-content: flex-start;\r\n        }\r\n    }\r\n    .promo_large.active {\r\n        opacity: 1;\r\n        pointer-events: all;\r\n    }\r\n}\r\n\r\n#gswpts_tabs {\r\n    /* colors */\r\n    $blue: $link-color;\r\n    $red: #ff4a53;\r\n    $dark: #333;\r\n    $accent: $blue;\r\n    $accent-inactive: desaturate($accent, 85%);\r\n    $secondary: $accent-inactive;\r\n    /* tab setting */\r\n    $tab-count: 4;\r\n    $indicator-width: 50px;\r\n    $indicator-height: 4px;\r\n    /* breakpoints */\r\n    $breakpoints: (\r\n        medium: #{$tab-count * 250px},\r\n        small: #{$tab-count * 150px},\r\n    );\r\n    /* selectors relative to radio inputs */\r\n    $label-selector: \"~ ul > li\";\r\n    $slider-selector: \"~ .slider\";\r\n    $content-selector: \"~ .content > section\";\r\n    @mixin tabs(\r\n        $label-selector: $label-selector,\r\n        $slider-selector: $slider-selector,\r\n        $content-selector: $content-selector\r\n    ) {\r\n        @for $i from 1 through $tab-count {\r\n            &:nth-of-type(#{$i}):checked {\r\n                #{$label-selector}:nth-child(#{$i}) {\r\n                    @content;\r\n                }\r\n                #{$slider-selector} {\r\n                    transform: translateX(#{100% * ($i - 1)});\r\n                }\r\n                #{$content-selector}:nth-child(#{$i}) {\r\n                    display: block;\r\n                }\r\n            }\r\n        }\r\n    }\r\n    .tabs {\r\n        padding: 20px;\r\n        width: 100%;\r\n        background-color: $bg-color;\r\n        border-radius: $border-radious;\r\n        min-width: #{$tab-count * 60px};\r\n        input[name=\"tab-control\"] {\r\n            display: none;\r\n        }\r\n        .content section h2,\r\n        ul li label {\r\n            font-weight: bold;\r\n            font-size: 18px;\r\n            color: $accent;\r\n        }\r\n        ul {\r\n            list-style-type: none;\r\n            padding-left: 0;\r\n            display: flex;\r\n            flex-direction: row;\r\n            margin-bottom: 10px;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            flex-wrap: wrap;\r\n            li {\r\n                box-sizing: border-box;\r\n                flex: 1;\r\n                width: #{100% / $tab-count};\r\n                padding: 0 10px;\r\n                text-align: center;\r\n                label {\r\n                    transition: all 0.3s ease-in-out;\r\n                    color: $secondary;\r\n                    padding: 5px auto;\r\n                    overflow: hidden;\r\n                    text-overflow: ellipsis;\r\n                    display: block;\r\n                    cursor: pointer;\r\n                    transition: all 0.2s ease-in-out;\r\n                    white-space: nowrap;\r\n                    -webkit-touch-callout: none;\r\n                    br {\r\n                        display: none;\r\n                    }\r\n                    svg {\r\n                        fill: $secondary;\r\n                        height: 1.2em;\r\n                        vertical-align: bottom;\r\n                        margin-right: 0.2em;\r\n                        transition: all 0.2s ease-in-out;\r\n                    }\r\n                    &:hover,\r\n                    &:focus,\r\n                    &:active {\r\n                        outline: 0;\r\n                        color: lighten($secondary, 15%);\r\n                        svg {\r\n                            fill: lighten($secondary, 15%);\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        .slider {\r\n            position: relative;\r\n            width: #{100% / $tab-count};\r\n            transition: all 0.33s cubic-bezier(0.38, 0.8, 0.32, 1.07);\r\n            .indicator {\r\n                position: relative;\r\n                width: $indicator-width;\r\n                max-width: 100%;\r\n                margin: 0 auto;\r\n                height: $indicator-height;\r\n                background: $accent;\r\n                border-radius: 1px;\r\n            }\r\n        }\r\n        .content {\r\n            margin-top: 25px;\r\n            section {\r\n                display: none;\r\n                animation: {\r\n                    name: content;\r\n                    direction: normal;\r\n                    duration: 0.